.albums-container {
  margin: 20px;
  font-family: Arial, sans-serif;
}

.albums-list {
  list-style-type: none;
  padding: 0;
}

.album-item {
  margin-bottom: 10px;
}

.album-link {
  display: inline-block;
  text-decoration: none;
}

.album-button {
  padding: 8px 16px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  text-decoration: none;
}

.album-button:hover {
  background-color: #0056b3;
  text-decoration: underline;
}